.cancell{
    width: 50px;
    height: 50px;
    border-radius: 30px;
    opacity: 0.4;
    background-color: grey;
    padding: 0;  
    position: absolute;
    top: 20px;
    left: 15px;
}
img{
    width: 100%;
}
.cancelTab{
    font-size: 50px;
   position: absolute;
   top: -10px;
   left: 4px;
   color: whitesmoke;
}
.bot{
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 70px;
    background-color: lightgray;  
}
.botImg{
    height: 100%;
    width: 10%;
    float: left;
    text-align: center;
    padding-top: 10px;
    margin-left: 10px;;
}
.bot i{
    font-size: 30px;
}
.botbtn{
    width: 27%;
    float: left;
    height: 35%;
    text-align: center;
    padding-bottom: 8px;
    padding-top: 8px;
    margin-top: 15px;
    color: whitesmoke;
}
#add{
    margin-left: 15px;
    border-radius: 20px 0 0 20px;
    background-color: darkorange;
}
#buy{
    border-radius: 0 20px 20px 0;
    background-color: tomato;
}